import React, { useState, useEffect } from "react";
import { Link } from 'react-router-dom';
import axios from "axios";
import "./AdminMember.css";
import AdminMemberModal from "./AdminMemberModal"; // Modal  import
import "./AdminMember.css";
import Sidebar from '../Sidebar/Sidebar';



function AdminMember() {
    const [members, setMembers] = useState([]);
    const [searchTerm, setSearchTerm] = useState("");
    const [selectedMember, setSelectedMember] = useState({});
    const [isModalOpen, setIsModalOpen] = useState(false);

    const handleMemberDetails = (member) => {
        setSelectedMember(member);
        setIsModalOpen(true);
    }

    const handleCloseModal = () => {
        setSelectedMember({});
        setIsModalOpen(false);
    }

    const getAllMembers = function () {
        axios.get("http://localhost/web/members")
            .then((response) => {
                return response.data;
            })
            .then((result) => {
                if (Array.isArray(result.data)) {
                    setMembers(result.data);
                    console.log(result.data);
                } else {
                    console.log("  .");
                }
            })
            .catch((exception) => {
                alert("     !");
                console.log(exception);
            });
    };

    useEffect(() => {
        getAllMembers();
    }, []);

    const handleSearchChange = (event) => {
        setSearchTerm(event.target.value);
    };

    const filteredMembers = members.filter((member) => {
        return (
            member.name.toLowerCase().includes(searchTerm.toLowerCase()) ||
            member.nickname.toLowerCase().includes(searchTerm.toLowerCase()) ||
            member.email.toLowerCase().includes(searchTerm.toLowerCase())
        );
    });
    return (
        <div className="adminMember">
            <div className="adminMember-header">
                <Sidebar />
                <h2> </h2>
                <input
                    id="searchMember"
                    type="text"
                    placeholder=" "
                    value={searchTerm}
                    onChange={handleSearchChange}
                    className="adminMember-input"
                />
            </div>
            <table className="adminMember-table">
                {/*   */}
                <thead>
                    <tr>
                        <th></th>
                        <th></th>
                        <th>NickName</th>
                        <th>Email</th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                    </tr>
                </thead>
                {/*   */}
                <tbody>
                    {filteredMembers.map((member) => (
                        <tr key={member.no}>
                            <td>{member.no}</td>
                            <td>{member.name}</td>
                            <td>{member.nickname}</td>
                            <td>{member.email}</td>
                            <td>{member.tel}</td>
                            <td>{member.createdDate}</td>
                            <td>{member.state}</td>
                            <td>{member.auth}</td>
                            <td>
                                <button
                                    type="button"
                                    onClick={() => handleMemberDetails(member)}
                                    className="adminMember-detailsBtn"
                                >
                                    
                                </button>
                            </td>
                        </tr>
                    ))}
                </tbody>
            </table>
            {/* Modal  */}
            {isModalOpen && (
                <AdminMemberModal
                    isOpen={isModalOpen}
                    onClose={handleCloseModal}
                    selectedMember={selectedMember}
                />
            )}
        </div>
    );
}

export default AdminMember;