import React from 'react';
import './sign.css';
import { useState } from 'react';
import { Link } from "react-router-dom";
import axios from "axios";
import { useNavigate } from 'react-router-dom';

axios.defaults.withCredentials = true;

//------------------------------------------------------------------------------
function Sign() {

  const navigate = useNavigate();

  const signUp = function() {
    const form = document.querySelector("#member-form");
    const formData = new FormData(form);

    //      
    let isEmpty = false;
    for (const value of formData.values()) {
      if (!value) {
        isEmpty = true;
        break;
      }
    }
    if (isEmpty) {
      alert("   .");
      return;
    }

    let json = JSON.stringify(Object.fromEntries(formData));
    // console.log(json);
    fetch("http://localhost/web/members", {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: json,
    })
      .then((response) => {
        console.log(json);
        return response.json();
      })
      .then((result) => {
        if (result.status == "success") {
          alert(" !");
        } else {
          alert(" !");
          console.log(result.data);
        }
      })
      .catch((exception) => {
        alert("   !");
        console.log(exception);
      });

      navigate("/Main");
  };

  //------------------------------------------------------------------------------
  function checkEmail() {
    const email = document.querySelector('#f-email').value; //id "f-email"   
    const emailRegex = /^[a-zA-Z0-9]+@[a-zA-Z0-9]+\.[A-Za-z]{3,}$/; //     

    if (!emailRegex.test(email)) { //     
      document.querySelector('.email_already').style.display = 'none';
      document.querySelector('.email_ok').style.display = 'none';
      document.querySelector('.email_no').style.display = 'inline-block';
      return;
    } else {

      fetch("http://localhost/web/members/emailCheck?email=" + email, {
        method: 'GET',
        headers: {
          "Content-Type": "application/json",
        },
      })
        .then(response => response.json())
        .then(cnt => {
          if (cnt == 0) { //cnt 1 (=0 ) ->    
            document.querySelector('.email_ok').style.display = 'inline-block';
            document.querySelector('.email_already').style.display = 'none';
            document.querySelector('.email_no').style.display = 'none';
          } else { // cnt 1  ->   
            document.querySelector('.email_already').style.display = 'inline-block';
            document.querySelector('.email_ok').style.display = 'none';
            document.querySelector('.email_no').style.display = 'none';
            alert("  ");
            document.getElementById('f-email').value = '';
          }
        })
        .catch(error => {
          console.error(error);
          alert("");
        });
    };
  };

  //------------------------------------------------------------------------------
  function checkPW() {
    var pw = document.getElementsByName("password")[0].value;
    var num = pw.search(/[0-9]/g);
    var eng = pw.search(/[a-z]/ig);
    var spe = pw.search(/[`~!@@#$%^&*|'";:/?]/gi);

    if (pw.length < 6 || pw.length > 18) {
      alert("6 ~ 18  .");
      document.getElementById('f-password').value = '';
      return false;
    } else if (pw.search(/\s/) != -1) {
      alert("   .");
      document.getElementById('f-password').value = '';
      return false;
    } else if (num < 0 || eng < 0 || spe < 0) {
      alert(",,   .");
      document.getElementById('f-password').value = '';
      return false;
    } else {
      document.querySelector('.password_ok').style.display = 'inline-block';
      console.log("  .");
      return true;
    }
  }

  //---------------------------------- --------------------------------------------
  function checkPW2() {
    var p1 = document.getElementById('f-password').value;
    var p2 = document.getElementById('f-password2').value;
    if (p1 && p2) {
      if (p1 != p2) {
        alert("   .  .");
        document.getElementById('f-password').value = '';
        document.getElementById('f-password2').value = '';
        document.querySelector('.password_ok').style.display = 'none';
        document.querySelector('.password_ok2').style.display = 'none';
      } else {
        document.querySelector('.password_ok2').style.display = 'inline-block';
        return true;
      }
    }
  }

  //------------------------------------------------------------------------------
  function checkNickname() {
    const nickname = document.querySelector('#f-nickname').value;

    fetch("http://localhost/web/members/nickCheck?nickname=" + nickname, {
      method: 'GET',
      headers: {
        "Content-Type": "application/json",
      },
    })
      .then(response => response.json())
      .then(cnt => {
        if (cnt == 0) {
          document.querySelector('.nickname_ok').style.display = 'inline-block';
          document.querySelector('.nickname_already').style.display = 'none';
        } else {
          document.querySelector('.nickname_already').style.display = 'inline-block';
          document.querySelector('.nickname_ok').style.display = 'none';
          alert("  ");
          document.getElementById('f-nickname').value = '';
        }
      })
      .catch(error => {
        console.error(error);
        alert("");
      });
  };

  // ---------------------------------  -----------------------------------
  const [tel, setTel] = useState('');

  const hypenTel = (value) => {
    const processedValue = value.replace(/[^0-9]/g, '');
    const telLength = processedValue.length;

    if (telLength < 4) {
      return processedValue;
    } else if (telLength < 7) {
      return `${processedValue.slice(0, 3)}-${processedValue.slice(3)}`;
    } else if (telLength < 11) {
      return `${processedValue.slice(0, 3)}-${processedValue.slice(3, 6)}-${processedValue.slice(6)}`;
    } else {
      return `${processedValue.slice(0, 3)}-${processedValue.slice(3, 7)}-${processedValue.slice(7, 11)}`;
    }
  };

  const handleTelChange = (event) => {
    const value = event.target.value;
    const processedValue = hypenTel(value);
    setTel(processedValue);
  };



  return (
    <div className="signB">
      <div className="member-body">
        <div className="member-form-img">
          <img src='../img/logo2.png'></img>
        </div>
        <form id="member-form" className="member-form">
          <h3></h3>
          <table className='member-form-table'>
            <tbody>
              <tr>
                <th className="email-th">Email</th>
                <td><input id="f-email" type="email" name="email" className="email" placeholder="email  ." onInput={checkEmail} />
                  <span className="email_ok">  .</span>
                  <span className="email_already">    .</span>
                  <span className="email_no">   .</span>
                </td>
              </tr>
              <tr>
                <th className="password-th">Password</th>
                <td><input id="f-password" type="password" name="password" className="password" placeholder=", ,   6~18" onBlur={checkPW} />
                  <span className="password_ok">  .</span>
                </td>
              </tr>
              <tr>
                <th className="password-th">Confirm Password</th>
                <td><input id="f-password2" type="password" className="password" placeholder=" " onBlur={checkPW2} />
                  <span className="password_ok2"> .</span>
                </td>
              </tr>
              <tr>
                <th className="name-th">Name</th>
                <td><input type="name" name="name" className="name" placeholder=" ." /></td>
              </tr>
              <tr>
                <th className="nickname-th">NickName</th>
                <td><input id="f-nickname" type="nickname" name="nickname" className="nickname" placeholder=" ." onBlur={checkNickname} />
                  <span className="nickname_ok">  .</span>
                  <span className="nickname_already">    .</span>
                </td>
              </tr>
              <tr>
                <th className="tel-th">Tel</th>
                <td><input type="tel" name="tel" className="tel" maxLength="13" placeholder=" ." onChange={handleTelChange} value={tel} /></td>
              </tr>
            </tbody>
          </table>

          <div>
            <button type="submit" className="btn btn-primary" id="btn-insert" onClick={signUp}></button>
          </div>

        </form>
      </div>
    </div>
  );
}

export default Sign;
