import React, { useState, useEffect } from "react";
import "./hmodal.css";
import axios from "axios";
import ReactPlayer from "react-player";
import Player from "../DemoPlayer";

axios.defaults.withCredentials = true;

function HModal(props) {
  const { isOpen, onClose, isNo, userNo } = props;
  const [value, setValue] = useState("");

  const [image, setImage] = useState("img/heart.png");
  const [prevImage, setPrevImage] = useState("");

  const [data, setData] = React.useState([]);
  const [url, setUrl] = useState("");

  const [likeCnt, setLikeCnt] = useState();

  const [photo, setPhoto] = useState(null);

  const [writerImg, setWriterImg] = useState(null);

  //video player
  // const script = document.createElement("script");
  // const access_key = "97fc9870723593468099bc59a5c7459b"; // VPE SDK  
  // script.async = true;
  // script.src =
  //   "https://player.vpe.naverncp.com/ncplayer.js?access_key=" + access_key;
  // document.body.appendChild(script);

  const handleKeyDown = (event) => {
    if (event.keyCode === 13) {
      event.preventDefault(); //    
      insert();
    }
  };

  axios
    .get("http://localhost/web/like/cnt/" + isNo)
    .then((response) => {
      const result = response.data;
      setLikeCnt(result);
    })
    .catch((exception) => {});

  axios
    .get("http://localhost/web/like/" + isNo)
    .then((response) => {
      const result = response.data;

      const data = result.data;

      if (data === "false") {
        //    
        setImage("img/heart.png");
      } else if (data === "true") {
        //    
        setImage("img/colorheart.png");
      }
    })
    .catch((exception) => {
      alert(" !");
      console.log(exception);
    });

  const handleChange = (event) => {
    setValue(event.target.value);
  };

  function insert() {
    axios
      .post(
        "http://localhost/web/replys",
        {},
        {
          params: {
            boardNo: isNo,
            content: value,
          },
        }
      )
      .then((response) => {
        return response;
      })
      .then((result) => {
        if (result.status == "200") {
          window.location.reload();
        } else if (result.errorCode == "401") {
        } else {
          alert(" !");
        }
      })
      .catch((exception) => {
        alert(" !");
        console.log(exception);
      });
  }

  React.useEffect(() => {
    axios
      .get(
        "http://localhost/web/replys",
        {},
        {
          params: {
            boardNo: isNo,
          },
        }
      )
      .then((response) => {
        setData(response.data.data);
        const result = response.data;
      })
      .catch((error) => console.error(error));
  }, [isNo]);

  function handleClick() {
    if (image === "img/heart.png") {
      axios
        .post(
          "http://localhost/web/like",
          {},
          {
            params: {
              boardNo: isNo,
              memberNo: userNo,
            },
          }
        )
        .then((response) => {
          return response;
        })
        .then((result) => {
          if (result.status == "200") {
          } else if (result.errorCode == "401") {
          } else {
            alert(" !");
          }
        })
        .catch((exception) => {
          alert(" !");
          console.log(exception);
        });

      setPrevImage("img/heart.png");
      setImage("img/colorheart.png");
    } else {
      axios
        .post(
          "http://localhost/web/like/delete",
          {},
          {
            params: {
              boardNo: isNo,
              memberNo: userNo,
            },
          }
        )
        .then((response) => {
          return response;
        })
        .then((result) => {
          if (result.status == "200") {
          } else if (result.errorCode == "401") {
          } else {
            alert(" !");
          }
        })
        .catch((exception) => {
          alert(" !");
          console.log(exception);
        });

      setImage(prevImage);
      setPrevImage("");
    }
  }

  if (!isOpen) {
    return null;
  }

  axios
    .get(`http://localhost/web/boards/${isNo}`)
    .then((response) => {
      const result = response.data;

      if (result.status === "failure") {
        alert("   .");
        return;
      }
      const board = result.data;

      const file = board.writer.photo;
      const filepath = file.split(".com/")[1];
      setWriterImg(filepath);

      document.querySelector("input[name='title']").value = board.title;
      document.querySelector("#f-created-date").innerHTML = board.createdDate;
      document.querySelector("#f-writer-name").innerHTML =
        board.writer.nickname;
      document.querySelector("textarea[name='content']").value = board.content;

      let ul = "";
      board.attachedFiles.forEach((file) => {
        if (file.no == 0) return;
        let html = `
      <li id="li-${file.no}">
        <a href="https://kr.object.ncloudstorage.com/bitcamp-bucket22/board/${file.filepath}">${file.originalFilename}</a>
        [<a href="#" onClick="deleteFile(${board.no}, ${file.no}); return false;"></a>] 
      </li>`;
        setUrl(`${file.filepath}`);
        ul += html;
      });
      // document.querySelector("#f-files").innerHTML = ul;
      checkOwner(board.writer.no);
    })
    .catch((error) => console.error(error));

  function checkOwner(writerNo) {
    axios
      .get("http://localhost/web/auth/user")
      .then((response) => {
        if (response.data.status === "success") {
          if (response.data.data.no === writerNo) {
            document.querySelector("#btn-update").classList.remove("guest");
            document.querySelector("#btn-delete").classList.remove("guest");
          }
        }
      })
      .catch((error) => {
        alert("      !");
        console.log(error);
      });
  }

  function updateBtn() {
    const form = document.querySelector("#board-form");
    const formData = new FormData(form);

    axios
      .put("http://localhost/web/boards/" + isNo, formData)
      .then((response) => {
        if (response.data.status === "success") {
          window.location.href = "./";
        } else {
          alert(" !");
        }
      })
      .catch((error) => {
        alert("   !");
        console.log(error);
      });
  }

  function deleteBtn() {
    if (window.confirm(" ?")) {
      axios
        .delete("http://localhost/web/boards/" + isNo)
        .then((response) => {
          const result = response.data;
          if (result.status === "success") {
            alert(" .");
            window.location.reload();
          } else {
            alert("  !");
          }
        })
        .catch((error) => console.error(error));
    }
  }

  function commentDelete(commentNo) {
    axios
      .delete(`http://localhost/web/replys/${commentNo}`)
      .then((response) => {
        console.log(response.data);
        if (response.data.status === "success") {
          window.location.href = "./";
        } else {
          alert(" !");
        }
      })
      .catch((error) => {
        alert("   !");
        console.log(error);
      });
  }

  function formatDate(dateString, format) {
    const date = new Date(dateString);
    const options = {
      hour12: false,
      timeZone: "Asia/Seoul",
    };
    const parts = new Intl.DateTimeFormat("ko-KR", options).formatToParts(date);
    const year = date.getFullYear();
    const month = padZero(date.getMonth() + 1);
    const day = padZero(date.getDate());
    const hour = padZero(date.getHours());
    const minute = padZero(date.getMinutes());

    if (isWithin24Hours(dateString)) {
      //     24  
      if (new Date().getDate() === date.getDate()) {
        //      
        return `${hour}:${minute}`;
      } else {
        //        
        return `${year}-${month}-${day}`;
      }
    } else if (new Date().getDate() === date.getDate() + 1) {
      //   ,    
      return ``;
    } else {
      //     2  
      return `${year}-${month}-${day}`;
    }

    function isWithin24Hours(dateString) {
      const createdDate = new Date(dateString);
      const timeDiff = Date.now() - createdDate.getTime();
      return timeDiff < 24 * 60 * 60 * 1000;
    }

    function padZero(num) {
      return String(num).padStart(2, "0");
    }
  }


  return (
    <div
      className="hmodal-background"
      onClick={(e) => {
        //        .
        if (e.target.closest(".hmodal") !== null) {
          return;
        }
        onClose();
      }}
    >
      <div className="hmodal-overlay">
        <div className="hmodal">
          <form id="board-form" method="post" enctype="multipart/form-data">
            <div className="hmodal-view">
              <div className="hmodal-view-file">
                {/* <Player vurl={url} /> */}

                <ReactPlayer
                  url={url}
                  controls={true}
                  width="100%"
                  height="100%"
                />
              </div>

              <div className="hmodal-view-text">
                <div className="hmodal-view-header">
                  <div>
                    <div>
                      <input type="text" name="title" className="title-text" />
                    </div>
                    <div>
                      <span className="f-created-date">From</span>
                      <span id="f-created-date"></span>
                    </div>
                  </div>
                  <div className="heartbox">
                    <img
                      src={image}
                      className="heart"
                      onClick={handleClick}
                    ></img>
                    <div className="likeCntText">{likeCnt}</div>
                  </div>
                </div>

                <div className="hmodal-view-body">
                  <div className="hmodal-view-body-head">
                    <img
                      src={`http://xqhwurtrwszc16694936.cdn.ntruss.com/${writerImg}?type=f&w=40&h=40&ttype=jpg`}
                    />
                    <span id="f-writer-name"></span>
                  </div>
                  <div className="hmodal-view-body-body">
                    <textarea
                      name="content"
                      rows="10"
                      cols="51"
                      className="content-text"
                    ></textarea>
                    <input
                      type="file"
                      name="files"
                      multiple
                      className="file-input"
                    />
                    <ul id="f-files"></ul>
                  </div>
                </div>

                <div className="hmodal-view-footer">
                  <div className="hmodal-view-footer-f">
                    <div className="hmodal-view-footer-f-text"></div>

                    <div className="hmodal-view-footer-f-view">
                      <ul>
                        {data.map((item) => {
                          let file = item.writer.photo;
                          let filepath = file.split(".com/")[1];

                          if (item.boardNo === isNo) {
                            return (
                              <li key={item.no}>
                                <div className="comment-header">
                                  <div className="comment-header-writer">
                                    <img
                                      src={`http://xqhwurtrwszc16694936.cdn.ntruss.com/${filepath}?type=f&w=40&h=40&ttype=jpg`}
                                    ></img>
                                    <div className="comment-writer-nickname">
                                      {item.writer.nickname}
                                    </div>
                                  </div>
                                  <div className="comment-header-content">
                                    {item.content ? item.content : ""}
                                  </div>
                                </div>
                                <div className="comment-footer">
                                  <div className="comment-footer-createDate">
                                    {formatDate(item.createdDate)}
                                  </div>
                                  <button
                                    id="comment-delete"
                                    className="comment-delete"
                                    type="button"
                                    onClick={() => commentDelete(item.no)}
                                  >
                                    X
                                  </button>
                                </div>
                              </li>
                            );
                          } else {
                            return null; //       
                          }
                        })}
                      </ul>
                    </div>
                  </div>
                  <div className="hmodal-view-footer-s" method="post">
                    <input
                      type="text"
                      className="comment-text"
                      value={value}
                      onChange={handleChange}
                      onKeyDown={handleKeyDown}
                    />
                    <button
                      type="submit"
                      className="comment-btn"
                      onClick={(e) => {
                        e.preventDefault();
                        insert();
                      }}
                    >
                      
                    </button>
                  </div>
                </div>

                <div className="hmodal-view-btn">
                  <button
                    id="btn-update"
                    type="button"
                    class="guest"
                    onClick={updateBtn}
                  >
                    
                  </button>
                  <button
                    id="btn-delete"
                    type="button"
                    class="guest"
                    onClick={deleteBtn}
                  >
                    
                  </button>
                </div>
              </div>
            </div>
          </form>
        </div>
      </div>
    </div>
  );
}

export default HModal;
