import React, { useState, useEffect } from "react";
import { Link } from 'react-router-dom';
import axios from "axios";
import Sidebar from '../Sidebar/Sidebar';
import AdminBoardModal from "./AdminBoardModal";
import "./AdminBoard.css";

function AdminBoard() {
    const [boards, setBoards] = useState([]);
    const [searchTerm, setSearchTerm] = useState("");
    const [selectedBoard, setSelectedBoard] = useState({});
    const [isModalOpen, setIsModalOpen] = useState(false);

    const handleBoardDetails = (board) => {
        setSelectedBoard(board);
        setIsModalOpen(true);
    }

    const handleCloseModal = () => {
        setSelectedBoard({});
        setIsModalOpen(false);
    }

    const getAllBoards = function () {
        axios.get("http://localhost/web/boards")
            .then((response) => {
                return response.data;
            })
            .then((result) => {
                if (Array.isArray(result.data)) {
                    setBoards(result.data);
                    console.log(result.data);
                } else {
                    console.log("  .");
                }
            })
            .catch((exception) => {
                alert("     !");
                console.log(exception);
            });
    };

    useEffect(() => {
        getAllBoards();
    }, []);

    const handleSearchChange = (event) => {
        setSearchTerm(event.target.value);
    };

    const filteredboards = boards.filter((board) => {
        return (
            board.title.toLowerCase().includes(searchTerm.toLowerCase()) ||
            board.content.toLowerCase().includes(searchTerm.toLowerCase())
        );
    });

    return (
        <div className="adminBoard">
            <div className="adminBoard-header">
                <Sidebar />
                <h2> </h2>
                <input
                    id="searchboard"
                    type="text"
                    placeholder=" "
                    value={searchTerm}
                    onChange={handleSearchChange}
                    className="adminBoard-input"
                />
            </div>
            <table className="adminBoard-table">
                <thead>
                    <tr>
                        <th> </th>
                        <th>  </th>
                        <th></th>
                        <th> </th>
                        <th></th>
                        <th></th>
                        <th></th>
                    </tr>
                </thead>
                <tbody>
                    {filteredboards.map((board) => (
                        <tr key={board.no}>
                            <td>{board.no}</td>
                            <td>{board.boardTypeId}</td>
                            <td>{board.title}</td>
                            <td>{board.content}</td>
                            <td>{board.createdDate}</td>
                            <td>{board.viewCount}</td>
                            <td>
                                <button
                                    type="button"
                                    onClick={() => handleBoardDetails(board)}
                                    className="adminBoard-detailsBtn">
                                    
                                </button>
                            </td>
                        </tr>
                    ))}
                </tbody>
            </table>
            {/* Modal  */}
            {isModalOpen && (
                <AdminBoardModal
                    isOpen={isModalOpen}
                    onClose={handleCloseModal}
                    selectedBoard={selectedBoard}
                />
            )}
        </div>
    );
}

export default AdminBoard;
